import {isConvertible} from './isConvertible';

/**
 * This is a class that implement a interface
 */
export class Longitud implements isConvertible {
    constructor(public unidad: number) {

    }
    help() {
        console.log("Cree un objeto con la longitud en piés y con la función convertir se le devolverá en metros");
    }

    convertir() {
        return this.unidad * 0.3048;
    }
}